<template>
  <div class="tabbaritemwrap" @click="fn(mark)">
    <span v-show="!bol"><slot name="tabbar-normal-pic"></slot></span>
    <span v-show="bol"><slot name="tabbar-active-pic"></slot></span>
    <span class="tabbar-txt" :class="{'tabbar-active':bol}">{{ tabbartxt }}</span>

  </div>
</template>

<script>
export default {
  props:['tabbartxt','mark','sel'],
  computed:{
    bol:function(){
      if(this.mark === this.sel){
        return true
      }
      return false
    }
  },
  methods:{
    fn:function (mark) {
      this.$emit('change',mark)
    }
  }

}
</script>

<style>
  .tabbaritemwrap {width:25%;float:left;text-align:center;font-size:0;}
  .tabbaritemwrap img {width:1.5rem;height:1.4rem;}
  .tabbaritemwrap .tabbar-txt {font-size:0.65rem;display:block; color: #979797; letter-spacing:0.12rem;text-indent: 0.12rem;}
  .tabbaritemwrap .tabbar-active{color: #1348AE;}
</style>



