<template>
   <div>
     <Headeronloads headertits="登录">
       <span class="arrowspan" slot="arrow" @click="returnfun()"></span>

       <p class="onload-login" slot="onload-login"><router-link to="/Personage/Onload/Login">注册</router-link></p>
     </Headeronloads>
     <div class="onload-container">
       <Login-content onloadName="登录名" hint="手机号码/用户名/邮箱" types="text">
         <img src="../assets/images/chahao.png" alt="" slot="choice-pic">
       </Login-content>
       <Login-content onloadName="密码" hint="登录密码" types="password">
         <img src="../assets/images/mima.png" alt="" slot="choice-pic">
       </Login-content>
       <div class="sbumit-box">
         <input type="button" class="submit">
         <span>登录</span>
       </div>
       <div class="methods">
         <span>登录遇到问题?</span>
         <span>境外手机登录</span>
       </div>
       <div class="clearfix"></div>
       <div class="friendship-link">
         <span><img src="../assets/images/wxixin.png" alt=""></span>
         <span><img src="../assets/images/QQ.png" alt=""></span>
         <span><img src="../assets/images/zhifubao.png" alt=""></span>
         <span><img src="../assets/images/sina.png" alt=""></span>
         <span><img src="../assets/images/baidu.png" alt=""></span>
       </div>
     </div>
     <div class="shade"></div>
   </div>
</template>

<script>
import Headeronloads from './onload-item.vue'
import LoginContent from './onload-item-zhuce.vue'
  export default {
    components:{
       Headeronloads,
      LoginContent
     },
    methods:{
      returnfun:function () {
        window.history.go(-1);
      }
    }
  }
</script>

<style>
  .onload-login a{color: #534bdb;}
  .shade {width:100%;height:2.5rem;background:#fff;position:fixed;bottom:0;left:0;z-index: 99;}
  a {text-decoration: none;}
  .arrowspan {
    display:inline-block;
    width:1rem;
    height:1.5rem;
  }
.onload-container {width:17rem;margin:0 auto;margin-top:3rem;}
.submit {
  display:block;
  width:100%;
  background-color:#F7C612;
  outline-style: none;
  border:none;
  height:1.9rem;
  border-radius:0.2rem;
}
.sbumit-box {
  position:relative;
  margin-top:2rem;
}
  .sbumit-box>span {
    position:absolute;
    top:0.5rem;
    left:45%;
    color:#fff;
    font-size:0.8rem;
  }
  .methods {
    font-size:0.6rem;
    color: #929292;
    width:100%;
    margin-top:0.4rem;
  }
  .methods>span:nth-child(1){
    float:left;
  }
.methods>span:nth-child(2){
  float:right;
}
.friendship-link {
  margin-top:5rem;
  width:100%;
  display:flex;
  justify-content: space-around;
}
/*.friendship-link>span {*/
  /*float:left;*/
  /*margin-left:0.5rem;*/
/*}*/
  .friendship-link>span>img {
    width:1.75rem;
  }
</style>





