<template>
  <div class="tabbar">
    <router-link to="/Home">
      <tabbar-item tabbartxt="首页" :sel="selecded" mark="Home" @change="getval">
        <img src="../assets/images/tabbar/footer_0322_05.png" alt="" slot="tabbar-normal-pic">
        <img src="../assets/images/tabbar/footer_0322_01.png" alt="" slot="tabbar-active-pic">
      </tabbar-item>
    </router-link>
    <router-link to="/Destination">
      <tabbar-item tabbartxt="目的地" :sel="selecded" mark="Destination" @change="getval">
        <img src="../assets/images/tabbar/footer_0322_06.png" alt="" slot="tabbar-normal-pic">
        <img src="../assets/images/tabbar/footer_0322_02.png" alt="" slot="tabbar-active-pic">
      </tabbar-item>
    </router-link>
    <router-link to="/History">
      <tabbar-item tabbartxt="浏览历史" :sel="selecded" mark="History" @change="getval">
        <img src="../assets/images/tabbar/footer_0322_07.png" alt="" slot="tabbar-normal-pic">
        <img src="../assets/images/tabbar/footer_0322_03.png" alt="" slot="tabbar-active-pic">
      </tabbar-item>
    </router-link>
    <router-link to="/Personage">
      <tabbar-item tabbartxt="个人" :sel="selecded" mark="Personage" @change="getval">
        <img src="../assets/images/tabbar/footer_0322_08.png" alt="" slot="tabbar-normal-pic">
        <img src="../assets/images/tabbar/footer_0322_04.png" alt="" slot="tabbar-active-pic">
      </tabbar-item>
    </router-link>
  </div>

</template>


<script>
  import TabbarItem from './tabbar-item.vue'
  export default {
    components:{
      TabbarItem
    },
    data(){
      return{
        selecded:''
      }
    },
    created:function () {
      var hash = window.location.hash.split('/')[1];
//      console.log(window.location.hash)
      if(hash === ''){
        this.selected = 'Home';
        return
      }
      this.selecded =hash;
//      console.log(window.location.hash)
    },
    methods:{
      getval:function (val) {
        this.selecded = val
      }
    }
  }
</script>

<style>
  .tabbar {background-color:#EDEDED;height:2.5rem;width:100%;position: fixed;
    left:0;bottom:0; padding-top:0.2rem;box-sizing: border-box;}

</style>



