<template>
  <div>
    <div class="searchbox">
      <div>
        <div class="choice">
          <span class="leftdirection"></span>
          <p class="choicetit">选择目的地</p>
        </div>
      </div>
      <div class="searchinput">
        <input type="text" placeholder="春季旅游节"/>
      </div>
    </div>
    <div class="topkongbai"></div>

  </div>
</template>
<script>

  export default {
    props:['place']
  }
</script>

<style>
  .choice {width:100%;text-align: center;position:absolute;top:0.2rem;}
  .leftdirection {display:inline-block;position:absolute;left:0.7rem;top:0;transform: scale(0.7);
    background: url("../assets/images/icon/sprites.png") no-repeat -100px -33px;width:1.3rem;height:1.8rem;}
  .choicetit {color:#fff;margin-top:0.2rem;font-size:1rem;}
  .searchbox {
    width: 100%;
    height: 4.4rem;
    background: #1448AE;
    position: relative;
    top: 0;
    left: 0;
    /*z-index:99;*/
    display:flex;
    align-items: center;
  }
  .searchinput {
    width: 90%;
    height: 1.5rem;
    margin: 0 auto;
    margin-top:1.5rem;
    position: relative;

  }
  .searchinput::before {
    content: '';
    background: url("../assets/images/icon/galss_03.png") no-repeat;
    width: 1.7rem;
    height: 1.7rem;
    position: absolute;
    transform: scale(0.6);
    top: 0.1rem;
    left: 0;
  }
  .searchinput>input {
    width: 100%;
    height: 100%;
    outline-style: none;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 17px;
    color: #fff;
    font-size: 1rem;
    text-indent: 1.5rem;
    position: absolute;
    left:0;
    top: 40%;
    margin-top: -0.5rem;
  }
  .searchinput>input::-webkit-input-placeholder {
    color: #dddddd;
    font-size:0.9rem;
  }

</style>



