<template>
  <div>
    <div class="header-top">
      <div class="public-container">
        <div class="left-arrow"><slot name="arrow"></slot></div>
        <p class="header-text">{{ headertit }}</p>
        <p class="right-text">{{ headertext }}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['headertit','headertext']
}
</script>
<style>
.header-top {width:100%;height:2.2rem;background-color:#1448AE;}
.public-container {width:17.6rem;margin:0 auto;position:relative;}
.header-text {color:#fff;font-size:0.8rem;padding-top:0.55rem;}
.left-arrow {width:0.7rem;position:absolute;left:0.1rem;top:0.4rem; height:1.5rem;float:left;background:url("../assets/images/icon/sprites.png") no-repeat -102px -35px;}
.right-text {position:absolute;top:0.55rem;right:0.2rem;color:#fff;font-size:0.8rem;}

</style>




