<template>
  <div>
    <Headers-top headertit="全部订单" headertext="手机查单">
      <p slot="arrow" class="return" @click="returnfun()"></p>
    </Headers-top>
    <div class="order-list">
       <p class="order-icon"><img src="../assets/images/icon1.png" alt=""></p>
      <div class="order-right-list">
        <ul class="order-right-list-ul">
          <li>
            <span class="order-item" >三亚5日4晚半自助游</span>
            <span class="order-price">¥6512</span>
            <div class="clearfix"></div>
          </li>
          <li>
            <span class="daly">12-23至12-27</span>
            <span class="present">未提交</span>
          </li>
        </ul>
        <div class="clearfix"></div>
        <div class="reserve">
          <p class="continue-resever">继续预定</p>
          <p class="check-path">查看行程</p>
          <p class="cancel">取消</p>
        </div>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="order-foot">
      <div class="foot-choice-shaixuan">
        <img src="../assets/images/shaixuan.png" alt="">
        <span>筛选</span>
      </div>
      <div class="foot-choice-yuding">
        <img src="../assets/images/yuding.png" alt="">
        <span>预定时间</span>
      </div>
      <div class="foot-choice-dingdan">
        <span>有效订单</span>
      </div>
    </div>
    <div class="foot-dingdan">
      <p class="order-show-allorder">已显示全部订单</p>
      <p class="order-check">查看一年前订单</p>
      <span class="duigou"><img src="../assets/images/duigou.png" alt=""></span>
    </div>

  </div>
</template>

<script>
  import HeadersTop from './header-top-item.vue'
  export default {
    components:{
      HeadersTop
    },
    methods:{
      returnfun:function () {
        window.history.go(-1);
      }
    }

  }
</script>

<style>
  ul{list-style: none;}
  .clearfix {clear: both;}
.return {width:1rem;height:1.5em;background:transparent;}
  .order-list{width:17.5rem;margin:0 auto;background:#F4F4F4;border-radius:0.5rem;padding:0.5rem 0;margin-top:1rem;box-sizing: border-box;}
  .order-icon {margin-left:0.725rem;}
  .order-icon>img {width:0.925rem;height:0.925rem;float:left;}
  .order-right-list {float:left;margin-left:0.7rem;}
  .order-right-list-ul {width:14rem;float:right;}
  .order-right-list-ul>li {width:100%;line-height:1.4rem;}
  .order-item {font-size:0.825rem;color:#333;float:left;}
  .order-price {font-size:0.725rem;color:#F69F5C;float:right;}
.daly {font-size:0.725rem;color:#676767;float:left;}
.reserve {margin-top:1rem;}
.present {font-size:0.725rem;color:#676767;float:right;}
  .reserve>p {float:right;width:3.6rem;border-radius:0.2rem;height:1.425rem;line-height:1.425rem;
   border:1px solid #ccc;font-size:0.75rem;color:#1AB2D9;margin-left:0.3rem;}
  .reserve>.continue-resever {color:#fff;background-color:#1AB2DB;}
  .foot-choice-shaixuan {padding-top:0.3rem;}
.order-foot {width:100%;height:2.575rem;background:#999999;position:fixed;bottom:0;left:0;z-index:99;}
  .order-foot>div {width:33%;float:left;color:#fff;font-size:0.725rem;margin-top:0.4rem;}
  .order-foot img {width:1rem;vertical-align:middle;}
 .foot-choice-dingdan::after{display:inline-block;content:'';width:1rem;
   height:1rem;border:1px solid #fff;position:relative;top:0.3rem;left:0.3rem;}
  .order-show-allorder,.order-check {margin:0 auto;color: #959595;font-size:0.725rem;}
  .order-check {color: #787878;border:1px solid #ccc;width:6.75rem;height:1.25rem;line-height:1.25rem;margin-top:1rem;}
  .order-check::after {display:inline-block;content:'';width:0.4rem;
   transform: rotate(45deg); height:0.4rem;border:1px solid #000;border-bottom:0;border-left:0;}
  .foot-dingdan {position:fixed;bottom:5rem;right:6rem;}
  .duigou {position:absolute;right:-5rem;bottom:-2rem;}
  .duigou>img {width:1rem;}
</style>



