<template>
  <div>
    <Header-direction headertit="个人"></Header-direction>
    <div class="top-minebg"></div>
    <div class="mine-centerbg">
     <router-link to="/Personage/Onload"><p><img src="./../../assets/images/touxiang.png" alt=""></p></router-link>
      <h4>你好游客</h4>
      <p>世界很大，抬脚出去荡~</p>
      <div class="discounts">
        <div class="discounts-coupon">
          <p class="mine-price"><span>￥</span><span>50</span></p>
          <p class="red-packet ">红包优惠券</p>
        </div>
        <div class="integral">
          <p class="intergral-value"><span>1000</span><span>分</span></p>
          <p class="intergral">积分</p>
        </div>
      </div>
    </div>
    <router-link to="/Personage/Orders">
    <Icon-item personagetxt="全部订单">
      <p class="personage-list1 icon-size" slot="personage-icon"></p>
    </Icon-item>
    </router-link>
    <Icon-item personagetxt="待付款">
      <p class="personage-list2" slot="personage-icon"></p>
    </Icon-item>
    <Icon-item personagetxt="未出行">
      <p class="personage-list3" slot="personage-icon"></p>
    </Icon-item>
    <Icon-item personagetxt="待点评">
      <p class="personage-list4" slot="personage-icon"></p>
    </Icon-item>
    <div class="clearfix"></div>
    <div>
      <Personage-down-list mineclass="我的相册" classname="mine-navigation-pic">
        <p  slot="classname" class="mine-navigation-pic"><img src="../../assets/images/photo.png" alt=""  ></p>
        <img src="../../assets/images/icon/arrow_03.png" alt="" slot="arrowright">
      </Personage-down-list>
      <Personage-down-list mineclass="我的行程">
        <p slot="classname" class="mine-navigation-pic1"><img src="../../assets/images/mep.png" alt=""  ></p>
        <img src="../../assets/images/icon/arrow_03.png" alt="" slot="arrowright">
      </Personage-down-list>
      <Personage-down-list mineclass="我的游记">
        <p slot="classname" class="mine-navigation-pic2"><img src="../../assets/images/riji.png" alt=""  ></p>
        <img src="../../assets/images/icon/arrow_03.png" alt="" slot="arrowright">
      </Personage-down-list>
      <Personage-down-list mineclass="我的财富">
        <p slot="classname" class="mine-navigation-pic3"><img src="../../assets/images/caifu.png" alt=""  ></p>
        <img src="../../assets/images/icon/arrow_03.png" alt="" slot="arrowright">
      </Personage-down-list>

    </div>
    <div class="kongbaide"></div>
  </div>
</template>

<script>
  import HeaderDirection from '../../components/header-top-item.vue'
  import IconItem from '../../components/icon-item.vue'
  import PersonageDownList from '../../components/personage-down-list.vue'
export  default {
  components:{
    HeaderDirection,
    IconItem,
    PersonageDownList
  }
}
</script>

<style>
  .kongbaide {width:100%;height:20rem;}
  .clearfix {clear: both;}
.top-minebg {width:100%;height:3.05rem;background-color:#1449AF;margin-top:-1px;}
.mine-centerbg {width:100%;height:10.175rem;background-color:#EDF1F2;text-align:center;}
  .mine-centerbg p>img {width:4.5rem;margin-top:-2.25rem;}
.mine-centerbg>h4 {color:#898E94;font-size:0.9rem;font-weight:500;}
.mine-centerbg>p {color:#A9AAAC;font-size:0.7rem;}
.discounts {width:100%;display:flex;justify-content: space-around;margin-top:1.65rem;}
.discounts-coupon,.integral {width:50%;text-align:center;border-left:1px dashed #82888d;}
.mine-price>span:nth-child(1){font-size:0.9rem;color:#656565;}
.mine-price>span:nth-child(2){font-size:1.575rem;color:#656565;}
.red-packet {font-size:0.7rem;color:#A4ABB3;}
.intergral-value>span:nth-child(1) {font-size:1.575rem;color:#666B71;}
.intergral-value>span:nth-child(2) {font-size:0.7rem;color:#9BA0A3;}
.intergral {font-size:0.7rem;color:#969DA3;}
.personage-list1,.personage-list2,.personage-list3,.personage-list4{width:3.5rem;height:4.5rem;margin:0 auto;}
.personage-list1 {background:url("../../assets/images/icon/sprites.png") no-repeat -1rem -24.7rem /774%;}
.personage-list2 {background:url("../../assets/images/icon/sprites.png") no-repeat -5.1rem -24.7rem /774%;}
.personage-list3 {background:url("../../assets/images/icon/sprites.png") no-repeat -10.1rem -27.8rem /774%;width:3.9rem;}
.personage-list4 {background:url("../../assets/images/icon/sprites.png") no-repeat -14.8rem -27.8rem /774%;width:3.9rem;}

</style>



