<template>
 <div>

   <div class="temai-wrap">
      <p> <slot name="temai-pic"></slot> </p>
     <div class="temai-info">
       <p class="temai-info-content"> {{ temaicontentinfo }} </p>
       <div class="temai-info-price">
         <span> {{ xwcz }} </span>
         <p>
           <span>￥</span>
           <span> {{ prices }} </span>
           <i>起</i>
         </p>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
  export default {
    props:['temaicontentinfo','xwcz','prices']
  }
</script>
<style>
  .temai-wrap {width:8.55rem;height:9rem;border:1px solid #ccc;margin-top:0.6rem;}
  .temai-wrap img {width:100%;}
  .temai-info {width:95%;margin:0 auto;font-size:0.7rem;}
  .temai-info .temai-info-content {font-size:0.7rem;}
  .temai-info .temai-info-price {display:flex;justify-content: space-around;margin:0.1rem 0;color: #b4b4b4;}
  .temai-info .temai-info-price>span {position:relative;top:0.1rem;}
  .temai-info .temai-info-price>p>span {color:#FE9900;}
  .temai-info .temai-info-price>p>span:nth-of-type(2) {font-size:1rem;}
  .temai-info .temai-info-price>p>span:nth-of-type(1) {position:relative;top:-0.2rem;left:0.2rem;}
  i {font-style: normal;}

</style>



