<template>
  <div>
    <!--旅游-->
    <div class="section-nav-item">
      <div class="section-nav-item-left" :class="{itemly:bol,itemjp:boljp,itemjd:boljd}">
        <span>{{ productTxt }}</span>
      </div>
      <div class="section-nav-item-right">
        <div class="section-nav-item-right-item" :class="{lyitem1:bolly,jpitem1:boljp,jditem1:boljd}"><slot name="list1"></slot>
          <span> {{ listitem1 }}</span></div>
        <div class="section-nav-item-right-item" :class="{lyitem1:bolly,jpitem2:boljp,jditem2:boljd}"><slot name="list2"></slot>
          <span> {{ listitem2 }}</span></div>
        <div class="section-nav-item-right-item" :class="{lyitem2:bolly,jpitem3:boljp,jditem3:boljd}"><slot name="list3"></slot>
          <span> {{ listitem3 }}</span></div>
        <div class="section-nav-item-right-item" :class="{lyitem3:bolly,jpitem4:boljp,jditem4:boljd}"><slot name="list4"></slot>
          <span> {{ listitem4 }}</span></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
//    props:['productTxt','item1','item2','item3','item4'],
    props:{
      productTxt:{
        type:String,
        default:''
      },
      listitem1:{
        type:String,
        default:''
      },
      listitem2:{
        type:String,
        default:''
      },
      listitem3:{
        type:String,
        default:''
      },
      listitem4:{
        type:String,
        default:''
      },
      bol:{
        type:Boolean,
        default:false
      },
      bolly:{
        type:Boolean,
        default:false
      },
      boljp:{
        type:Boolean,
        default:false
      },
      boljd:{
        type:Boolean,
        default:false
      },

    }
  }
</script>

<style>
  .section-nav-item {
    width:100%;
    display: flex;
    justify-content: space-between;
    margin-top: 0.575rem;
  }
  .section-nav-item-left {
    width: 5.45rem;
    height: 4.7rem;
    text-align: center;
  }
  .itemly {
    background: url("../assets/images/icon/btnlist0323_03.jpg") no-repeat;
    background-size: cover;
  }
  .itemjp {
    background: url("../assets/images/icon/btnlist0323_06.jpg") no-repeat;
    background-size: cover;
  }
  .itemjd {
    background: url("../assets/images/icon/btnlist0323_12.jpg") no-repeat;
    background-size: cover;
  }

  .section-nav-item-left>span {
    color: #fff;
    display:inline-block;
    font-size: 1rem;
    margin-top:0.5rem;
  }
  .section-nav-item-right {
    width: 12.2rem;
    height: 4.7rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  .section-nav-item-right-item {
    width: 6rem;
    height: 2.2rem;
    margin-bottom:0.3rem;
    font-size: 0.9rem;
    color: #fff;

    position:relative;
  }
  .section-nav-item-right>div>span {
    display: inline-block;
    margin-top:0.5rem;
    /*margin-left:0.5rem;*/
    font-size:0.8rem;
  }
  .lyitem1 {
    background-color: #FF9F26;
  }
  .lyitem2 {
    background-color: #DE4989;
  }
  .lyitem3 {
    background-color: #7F83E4;
  }
  .lyitem4 {
    background-color: #4596ed;
  }
  .jpitem1 {background:url("../assets/images/icon/btnlist0323_08.jpg") no-repeat;
  background-size:cover;}
  .jpitem2 {background-color: #3AACE0;}
  .jpitem3 {background-color: #31C6CC;}
  .jpitem4 {background-color: #33B6D2;}
  .jditem1 {
    background-color: #BAC731;
  }
  .jditem2 {
    background-color: #BAC731;
  }
  .jditem3 {
    background-color: #82C631;
  }
  .jditem4 {
    background:url("../assets/images/icon/btnlist0323_14.jpg") no-repeat;
    background-size:cover;
  }

</style>





