<template>
  <div>
    <div class="headertops">
      <div class="publiccontainers">
        <div class="leftarrows"><slot name="arrow"></slot></div>
        <p class="headertexts">{{ headertits }}</p>
        <p class="righttexts"><slot name="onload-login"></slot></p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props:['headertits','headertexts']
  }
</script>
<style>
  .headertops {width:100%;height:2.2rem;}
  .publiccontainers {width:17.6rem;margin:0 auto;position:relative;}
  .headertexts {color: #3b3b3b;font-size:0.9rem;padding-top:0.6rem;}
  .leftarrows {width:0.7rem;position:absolute;left:0.1rem;top:0.4rem; height:1.5rem;float:left;
    background:url("../assets/images/icon/sprites.png") no-repeat -145px -31px;transform: scale(0.7)}
  .righttexts {position:absolute;top:0.6rem;right:0.2rem;color:#1448AD;font-size:0.8rem;}

</style>




