
<template>
  <div >
    <Headeronload headertits="注册">
      <span class="arrowspan" slot="arrow" @click="returnfn()"></span>
    </Headeronload>
    <div class="onload-container">
      <Login-contents onloadName="国家/地区"  types="text">
        <span slot="lalu" class="dalu">中国大陆 86</span>
      </Login-contents>
      <Login-contents onloadName="手机号" hint="常用手机号" types="text">
        <img src="../assets/images/chahao.png" alt="" slot="choice-pic">
      </Login-contents>
      <Login-contents onloadName="验证码" hint="短信验证码" types="text">
        <div class="yanzhengma" slot="choice-pic">获取验证码</div>
      </Login-contents>
      <Login-contents onloadName="设置密码" hint="6~12位大小写字母、数字" types="text">
        <img src="../assets/images/mima.png" alt="" slot="choice-pic">
        <img src="../assets/images/chahao.png" alt="" slot="choice-pic2">
      </Login-contents>
      <div class="sbumit-box">
        <input type="button" class="submit">
        <span>注册</span>
      </div>
      <div class="fasong">
        <p class="fasong-send">重新发送</p>
        <p class="login-time">60s</p>
      </div>
    </div>
    <div class="shade"></div>
  </div>
</template>

<script>
  import Headeronload from './onload-item.vue'
  import LoginContents from './onload-item-zhuce.vue'
  export default {
    components:{
      Headeronload,
      LoginContents
    },
    methods:{
      returnfn:function () {
        window.history.go(-1);
      }
    }
  }
</script>

<style>
  .dalu {
    font-size: 0.7rem;
    margin-left:3.9rem;
  }
  .shade {width:100%;height:2.5rem;background:#fff;position:fixed;bottom:0;left:0;z-index: 99;}
  .onload-container {width:17rem;margin:0 auto;margin-top:3rem;}
  .arrowspan {
    display:inline-block;
    width:1rem;
    height:1.5rem;
  }
  .yanzhengma {
    width:5.375rem;
    height:1.65rem;
    border-radius:0.3rem;
    background-color:#ccc;
    text-align:center;
    line-height:1.65rem;
    color:#fff;
    font-size:0.7rem;
    margin-top:-0.5rem;
  }
  .sbumit-box {
    position:relative;
    margin-top:2rem;
  }
  .sbumit-box>span {
    position:absolute;
    top:0.5rem;
    left:45%;
    color:#fff;
    font-size:0.8rem;
  }
  .submit {
    display:block;
    width:100%;
    background-color:#F7C612;
    outline-style: none;
    border:none;
    height:1.9rem;
    border-radius:0.2rem;
  }
  .fasong {
    width:4.85rem;
    float:right;
    margin-top:2rem;
    font-size:0.7rem;
  }
.fasong-send {
  width:4.75rem;
  height:1.375rem;
  background:#ccc;
  border-radius:0.2rem;
  color:#fff;
  line-height: 1.375rem;
  text-align:center;
}
  .login-time {
  width:4.75rem;
  height:1.375rem;
  background:#ccc;
  border-radius:0.2rem;
  color:#fff;
    margin-top:0.4rem;
  line-height: 1.375rem;
  text-align:center;
}

</style>



