<template>
  <div class="login-choice">
    <slot name="lalu"></slot>
    <span class="login-choice-name">{{onloadName}}</span>
    <input :type="types" :placeholder="hint">
    <span class="choice-pic"><slot name="choice-pic"></slot></span>
    <span class="choice-pic2"><slot name="choice-pic2"></slot></span>
  </div>
</template>

<script>
export default {
  props:['onloadName','hint','types']
}
</script>

<style>
  .login-choice {
    width:100%;
    position:relative;
    text-align:center;
    border-bottom: 1px solid #565656;
    margin-top:0.8rem;
  }
  input::placeholder {
    color:#ccc;
    font-size:0.7rem;
  }
  .login-choice-name {
    font-size:0.7rem;
    position:absolute;
    left:0.1rem;
    top:0.2rem;
  }
  .login-choice>input {
    border:none;
    width:9rem;
    height:1.5rem;
    outline-style: none;
  }
  .choice-pic{
    position:absolute;
    right:0.2rem;
    top:0.3rem;
  }
  .choice-pic2{
    position:absolute;
    right:1.2rem;
    top:0.4rem;
  }

  .choice-pic>img {
    width:0.6rem;
  }
  .choice-pic2>img {
    width:0.6rem;
  }

</style>



