import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home/home.vue'
import Destination from '../pages/destination/destination.vue'
import History from '../pages/history/history.vue'
import Personage from '../pages/personage/personage.vue'
import Hot from '../pages/destination/destinationpages/hot.vue'
import Inland from '../pages/destination/destinationpages/inland.vue'
import Foreign from '../pages/destination/destinationpages/foreign.vue'
import Orders from '../components/all-orders.vue'
import Onload from '../components/onload.vue'
import Login from '../components/login.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',component: Home},
    {path: '/Home',component: Home},
    {path: '/Destination',component: Destination,
      children:[
        {path:'',component:Hot},
        {path:'hot',component:Hot},
        {path:'inland',component:Inland},
        {path:'foreign',component:Foreign}
      ]
    },
    {path: '/History',component: History},
    {path: '/Personage',component: Personage,},
    {path: '/Personage/Orders',component: Orders},
    {path: '/Personage/Onload',component: Onload},
    {path: '/Personage/Onload/Login',component: Login}
  ]
})



