<template>
  <div>
    <div class="search-box">
      <div class="search-input">
        <input type="text" placeholder="搜索旅行地/酒店/景点"/>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['place']
}
</script>

<style>
  .choice {width:100%;text-align: center;}
  .left-direction {display:inline-block;position:absolute;}
  .search-box {
    width: 100%;
    height: 2.8rem;
    background: rgba(0,0,0,.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index:99;
    display:flex;
    align-items: center;
  }
  .search-input {
    width: 90%;
    height: 1.5rem;
    margin: 0 auto;
    position: relative;

  }
  .search-input::before {
    content: '';
    background: url("../assets/images/icon/galss_03.png") no-repeat;
    width: 1.7rem;
    height: 1.7rem;
    position: absolute;
    transform: scale(0.6);
    top: 0.1rem;
    left: 0;
  }
  .search-input>input {
    width: 100%;
    height: 100%;
    outline-style: none;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 17px;
    color: #fff;
    font-size: 1rem;
    text-indent: 1.5rem;
    position: absolute;
    left:0;
    top: 40%;
    margin-top: -0.5rem;
  }
  .search-input>input::-webkit-input-placeholder {
    color: #dddddd;
    font-size:0.9rem;
  }
</style>



