<template>
  <div @click="fn(destinationMark)">
    <p v-show="!destinationBol"><slot name="pic"></slot></p>
    <p v-show="destinationBol"><slot name="pic1"></slot></p>
  </div>
</template>
<script>
  export default {
    props:['destinationMark','destinationsel'],
     created:function () {
       if(this.destinationMark === this.destinationsel){
         return true
       }
       return false
     },
    computed:{
      destinationBol:function(){
        if(this.destinationMark === this.destinationsel){
          return true
        }
        return false
      }
    },
    methods:{
      fn:function (destinationMark){
        this.$emit('change',destinationMark);
//       this.$router.push('/'+this.mark);
//       console.log(this.mark)
      }
    }
  }
</script>

<style>

</style>



