    <div class="pestination-second-nav">
      <div v-for="item in msg">
        <div class="hot-nav"  @click="select(item.hash)" :class="{hotNavBgActive:item.hash==selecte}">
          <router-link :to="item.hash">
            <Shows :destinationMark="item.destinationMark" :destinationsel="selected" @change="getval">
              <p class="picc" slot="pic"><img :src="item.srcnormal" alt=""></p>
              <p class="picc" slot="pic1"><img :src="item.srcactive" alt=""></p>
            </Shows>
            <p class="left-title" :class="{'left-tit':item.hash==selecte}" v-text="item.destinationTxt"></p>
          </router-link>
        </div>
      </div>
    </div>
  </div>

</template>

