            srcnormal:'src/assets/images/remennormal.png',
            srcactive:'src/assets/images/remenactive.png',
            destinationMark:'hot',
            hash: '/Destination/hot',
            activedestinationbg: 'hot-picactive',
            destinationbg: 'hot-pic',
            destinationTxt: '热门'
          },
          {
            text: '国内',
            srcnormal:'src/assets/images/guoneinormal.png',
            srcactive:'src//assets/images/guonei.png',
            destinationMark:'inland',
            hash: '/Destination/inland',
            activedestinationbg: 'hot-pic1-active',
            destinationbg: 'hot-pic1',
            destinationTxt: '国内'
          },
          {
            text: '国外',
            srcnormal:'src//assets/images/guowai.png',
            srcactive:'src//assets/images/guowaiactive.png',
            destinationMark:'foreign',
            hash: '/Destination/foreign',
            activedestinationbg: 'hot-pic2-active',
            destinationbg: 'hot-pic2',
            destinationTxt: '国外'
          },
        ],
        selected:'hot',
        selecte: '/Destination/hot'
      }
    },
//    created:function () {
//      var hash = window.location.hash.substr(1)
//      console.log(hash)
//      if(hash === '/Destination'){
//        this.selected = '/Destination/hot';
//        return
//      }
//      this.selected = hash
//
//    },
    methods: {
      select: function (s) {
        this.selecte = s

      },
      getval:function(val){
        this.selected = val
//        console.log(this.selected)
      }
    }
  }
</script>
<style>
  .pestination-second-nav {width:3.6rem;height:28rem;
    background-color: #F4F4F4;float:left;}
  .pestination-second-nav {width:3.6rem;text-align: center;}
  .hot-nav {text-align: center;border-left: 0.1rem solid transparent;box-sizing: border-box;padding-top:1rem;padding-bottom:1rem;}
  /*.hot-pic {width:2.5rem;height:2.5rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -456px -102px;}*/
  /*.hot-picactive {width:2.5rem;height:2.5rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -456px -180px;}*/
.picc {width:2.5rem;height:2.5rem; margin:0 auto;}
.picc>img {width:2rem;height:2rem; }
  /*.hot-pic1 {width:2.5rem;height:2.6rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -540px -100px;}*/
  /*.hot-pic1-active {width:2.5rem;height:2.6rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -540px -180px;}*/

  /*.hot-pic2 {width:2.5rem;height:2.6rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -627px -100px;}*/
  /*.hot-pic2-active {width:2.5rem;height:2.6rem; margin:0 auto;transform:scale(0.9);*/
    /*background:url("../assets/images/icon/sprites.png") no-repeat -626px -180px;}*/

  .pestination-second-nav .hot-nav.hotNavBgActive{background-color:#fff;
    border-left: 0.1rem solid #F7C612;}
  .left-title {color:#666666;font-size:1rem;}
  .left-tit{color:#1449B1;}
  .pestination-content-right {float:left;margin-left:0.225rem;width:14.7rem;}
  .pestination-list {width:7.1rem;height:6.6rem;border:1px solid #ccc;
    float: left;margin-top:0.45rem;}
  .pestination-content-right .pestination-list:nth-of-type(2n){
    margin-left:0.2rem;
  }
  a {text-decoration: none;color:#ccc;}
  .pestination-list img {width:100%;}
  .pestination-list-tit {font-size:0.7rem;text-align: center;}
</style>



